:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 26%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(229, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --text-color1: var(--light-text1);
  --text-color2: var(--light-text2);
  --card-bg: var(--light-card);
  --card-bg-hover: var(--light-card-hover);
  --toggle-text: var(--toggle-light);
  --toggle-text-hover: var(--toggle-light);
  --toggle-bg: var(--toggle-light);
  --toggle-button: var(--light-bg);
  --toggle-bg-hover: linear-gradient(225deg, var(--toggle-bg-end), var(--toggle-bg-start));
  --toggle-button-hover: var(--dark-bg);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color1: var(--dark-text1);
    --text-color2: var(--dark-text2);
    --card-bg: var(--dark-card);
    --card-bg-hover: var(--dark-card-hover);
    --toggle-text: var(--dark-text1);
    --toggle-text-hover: var(--light-bg);
    --toggle-bg: linear-gradient(225deg, var(--toggle-bg-end), var(--toggle-bg-start));
    --toggle-button: var(--dark-bg);
  }
}
body.light {
  --background: var(--light-bg);
  --text-color1: var(--light-text1);
  --text-color2: var(--light-text2);
  --card-bg: var(--light-card);
  --card-bg-hover: var(--light-card-hover);
  --toggle-text: var(--toggle-light);
  --toggle-text-hover: var(--toggle-light);
  --toggle-bg: var(--toggle-light);
  --toggle-button: var(--light-bg);
  --toggle-bg-hover: linear-gradient(225deg, var(--toggle-bg-end), var(--toggle-bg-start));
  --toggle-button-hover: var(--dark-bg);
}

body.dark {
  --background: var(--dark-bg);
  --text-color1: var(--dark-text1);
  --text-color2: var(--dark-text2);
  --card-bg: var(--dark-card);
  --card-bg-hover: var(--dark-card-hover);
  --toggle-text: var(--dark-text1);
  --toggle-text-hover: var(--light-bg);
  --toggle-bg: linear-gradient(225deg, var(--toggle-bg-end), var(--toggle-bg-start));
  --toggle-button: var(--dark-bg);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--background);
  color: var(--text-color2);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5625rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: bold;
  color: var(--text-color1);
}

.toggle {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0 0.875rem;
  margin: 0;
  padding: 0;
  border: 0;
  transition: all 0.1s ease-out;
}
.toggle:hover label {
  color: var(--toggle-text-hover);
}
.toggle:hover input[type=radio] {
  cursor: pointer;
}
.toggle label {
  color: var(--toggle-text);
  font-size: 0.875rem;
  font-weight: 700;
}
.toggle__wrapper {
  height: 1.5rem;
  width: 3rem;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -2px;
  width: 1.5rem;
  height: 1.5rem;
  position: relative;
  z-index: 1;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  transform: scale(1.15);
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: var(--toggle-bg);
  transition: all 0.2s ease-out;
}
.toggle__button {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 0.4s ease-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 1.3125rem);
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background-color: var(--card-bg);
  border-radius: 5px;
  color: var(--text-color1);
  flex: 1;
  text-align: center;
  padding: 1.875rem 1.5625rem 1.5625rem;
  position: relative;
  overflow: hidden;
  transition: all 0.2s ease-out;
}
.card:hover {
  background-color: var(--card-bg-hover);
}
.card::before {
  content: "";
  display: block;
  height: 0.3125rem;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.card--facebook::before {
  background: var(--facebook);
}
.card--twitter::before {
  background: var(--twitter);
}
.card--instagram::before {
  background: linear-gradient(to right, var(--instagram-start), var(--instagram-end));
}
.card--youtube::before {
  background: var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
  height: 1.25rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--text-color2);
  font-weight: 700;
  letter-spacing: -2px;
  line-height: 1;
  margin-bottom: 4px;
}
.card__count--lg {
  font-size: 3.5rem;
}
.card__count--sm {
  font-size: 2rem;
}
.card__label {
  color: var(--dark-text1);
  font-size: 0.75rem;
  font-weight: 400;
  letter-spacing: 5px;
  text-transform: uppercase;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--up::before {
  content: "";
  background-image: url("../../../images/icon-up.svg");
  width: 8px;
  height: 4px;
  margin-right: 0.375rem;
}
.card__change--down {
  color: var(--brightred);
}
.card__change--down::before {
  content: "";
  background-image: url("../../../images/icon-down.svg");
  width: 8px;
  height: 4px;
  margin-right: 0.25rem;
}

.card.card-grid {
  padding-top: 1.5625rem;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  gap: 1.5625rem 0;
  align-items: center;
  justify-items: start;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count, .card-grid .card__change {
  align-self: end;
}
.card-grid img, .card-grid .card__change {
  justify-self: end;
}